.button-exit{cursor:pointer;justify-self:end;background-color:rgba(0,0,0,0);border:none;background-repeat:no-repeat;background-image:url(../../images/icon-exit.svg);width:18px;height:18px}.button-exit:hover{opacity:.7}@media screen and (min-width: 768px){.button-exit{background-image:none;position:relative;border-radius:8px;border:1px solid #f8f8f8;text-align:center;width:81px;cursor:pointer;min-height:38px;color:#f8f8f8}.button-exit::after{content:"Выход";position:absolute;top:8px;left:16px}}@media screen and (min-width: 768px){.button-exit{font-weight:400;font-size:16px;line-height:136.19%}}