.button-go-back{background-color:rgba(0,0,0,0);border:none;background-repeat:no-repeat;cursor:pointer;background-image:url(../../images/icon-go-back.svg);width:7px;height:14px;background-position:center}.button-go-back:hover{opacity:.7}@media screen and (min-width: 768px){.button-go-back{background-image:none;position:relative;border-radius:8px;border:1px solid #f8f8f8;text-align:center;width:81px;cursor:pointer;min-height:38px;color:#f8f8f8}.button-go-back::after{content:"Назад";position:absolute;top:8px;left:16px}}@media screen and (min-width: 768px){.button-go-back{font-weight:400;font-size:16px;line-height:136.19%}}